/**
 * Site header
 */
.site-header {
  border-top: 5px solid $grey-color-dark;
  border-bottom: 1px solid $grey-color-light;
  min-height: $spacing-unit * 1.865;

  // Positioning context for the mobile navigation icon
  position: relative;
}

.site-title {
  @include relative-font-size(1.925);
  font-weight: 300;
  line-height: $base-line-height * $base-font-size * 2.25;
  letter-spacing: -1px;
  margin-bottom: 0;
  float: left;
   margin: 0;
  position: absolute;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  margin-left: 1.75rem;

   @include media-query($on-palm) {
    margin-left: 0.75rem;
  }

  &,
  &:visited {
    color: lighten($grey-color-dark, 30%);
  }
}

.site-nav {
  float: right;
  line-height: $base-line-height * $base-font-size * 2.25;

  .nav-trigger {
      display: none;
  }

  .menu-icon {
    display: none;
  }

  .page-link {
    color: $text-color;
    line-height: $base-line-height;

    // Gaps between nav items, but not on the last one
    &:not(:last-child) {
      margin-right: 20px;
    }
  }

   @include media-query($on-palm) {
    .page-link {
      &:not(:last-child) {
        margin-right: 10px;
      }
    }
  }

  @include media-query($on-palm) {
    position: absolute;
    top: 9px;
    right: $spacing-unit / 2;
    background-color: $background-color;
    border: 1px solid $grey-color-light;
    border-radius: 5px;
    text-align: right;

    label[for="nav-trigger"] {
      display: block;
      float: right;
      width: 36px;
      height: 36px;
      z-index: 2;
      cursor: pointer;
    }

    .menu-icon {
      display: block;
      float: right;
      width: 36px;
      height: 26px;
      line-height: 0;
      padding-top: 10px;
      text-align: center;

      > svg {
        fill: lighten($grey-color-dark, 30%);
      }
    }

    input ~ .trigger {
      clear: both;
      display: none;
    }

    input:checked ~ .trigger {
      display: block;
      padding-bottom: 5px;
    }

    .page-link {
      display: block;
      padding: 5px 10px;

      &:not(:last-child) {
        margin-right: 0;
      }
      margin-left: 20px;
    }
  }
}



/**
 * Site footer
 */
.site-footer {
  border-top: 1px solid $grey-color-light;
  padding: $spacing-unit 0;
}

.footer-heading {
  @include relative-font-size(1.125);
  margin-bottom: $spacing-unit-bottom / 2;
}

.contact-list,
.social-media-list {
  list-style: none;
  margin-left: 0;
}

.footer-col-wrapper {
  @include relative-font-size(0.9375);
  color: $grey-color;
  margin-left: -$spacing-unit / 2;
  @extend %clearfix;
}

.footer-col {
  float: left;
  margin-bottom: $spacing-unit-bottom / 2;
  padding-left: $spacing-unit / 2;
}

.footer-col-center {
  float: left;
  margin-bottom: $spacing-unit-bottom / 2;
  margin-top: $spacing-unit-bottom / 2;
  margin-left: $spacing-unit-bottom / 6;
}

.footer-col-1 {
  width: -webkit-calc(35% - (#{$spacing-unit} / 2));
  width:         calc(35% - (#{$spacing-unit} / 2));
}

.footer-col-2 {
  width: -webkit-calc(20% - (#{$spacing-unit} / 2));
  width:         calc(20% - (#{$spacing-unit} / 2));
}

.footer-col-3 {
  float: right;
}

@include media-query($on-laptop) {
  .footer-col-1,
  .footer-col-2 {
    width: -webkit-calc(50% - (#{$spacing-unit} / 2));
    width:         calc(50% - (#{$spacing-unit} / 2));
  }

  .footer-col-3 {  
	float: right;
  }
}

@include media-query($on-palm) {
  .footer-col {
    float: none;
    width: -webkit-calc(100% - (#{$spacing-unit} / 2));
    width:         calc(100% - (#{$spacing-unit} / 2));
  }
}



/**
 * Page content
 */
.page-content {
  padding: $spacing-unit 0;
  flex: 1;
}

.page-heading {
  @include relative-font-size(2);
}

.post-list-heading {
  @include relative-font-size(1.75);
}

.post-list {
  margin-left: 0;
  list-style: none;

  > li {
    margin-bottom: $spacing-unit-bottom;
  }
}

.post-meta {
  font-size: $small-font-size;
  color: $grey-color;
}

.post-link {
  display: block;
  @include relative-font-size(1.4);
}



/**
 * Posts
 */
.post-header {
  margin-bottom: $spacing-unit-bottom;
}

.post-title {
  @include relative-font-size(2.0);
  letter-spacing: -1px;
  line-height: 1;
  margin-bottom: 10px;
  margin-top: 40px;

  @include media-query($on-laptop) {
    @include relative-font-size(1.15);
  }
}

.post-content {
  margin-bottom: $spacing-unit-bottom;

  @include media-query($on-palm) {
    margin-bottom: $spacing-unit-bottom / 2;
  }

  h2 {
    @include relative-font-size(2);

    @include media-query($on-laptop) {
      @include relative-font-size(1.75);
    }
  }

  h3 {
    @include relative-font-size(1.625);

    @include media-query($on-laptop) {
      @include relative-font-size(1.375);
    }
  }

  h4 {
    @include relative-font-size(1.25);

    @include media-query($on-laptop) {
      @include relative-font-size(1.125);
    }
  }
}

.post-img {
  object-fit: cover;
  height: 400pt;
  width: 100%;
  float:center;
}


/**
 * Publications
 */

.publication {
	margin-bottom: 60pt;
	text-align: start;

  @include media-query($on-palm) {
    margin-bottom: 30pt;
  }
}

.publication-preview {
  object-fit: cover;
  width:160px;
  height:160px;
  float:left;
  margin-right: 15pt;
  border: 1px solid lighten($grey-color, 22%);

  @include media-query($on-palm) {
    margin-right: 5pt;
  }
  border-radius: 7%;
  -webkit-border-radius: 7%;
  -moz-border-radius: 7%;
}
 
.publication-title {
  @include relative-font-size(2.625);
  letter-spacing: -1px;
  line-height: 1;

  @include media-query($on-laptop) {
    @include relative-font-size(2.25);
  }
}

.publication-info {
  @include relative-font-size(1.0);
  color: $grey-color;  
  margin-bottom: 5pt;
}

.publication-authors {
  @include relative-font-size(1.0);
  color: #565656ff;
  margin-top: -8pt;
  margin-bottom: 5pt;

}

.publication-link {
  display: block;
  @include relative-font-size(1.5);
}

.publication-asset{
margin-left: 10pt;
}

.publication-icon {
	width: 16pt;
    height: 16pt;
    display: inline-block;
    fill: #{$grey-color};
    padding: 1pt;
    vertical-align: middle;
}

.publication-icon-title {
	 margin-right: 5pt;
     @include relative-font-size(1.1);
	 vertical-align: middle;
}
 
 

/**
 * Custom elements
 */
 
 .avatar {
  margin-top: 15pt;
  margin-bottom: 1.0rem;
  margin-left: 4.0rem;
  border-radius: 100%;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  max-width: 120px;
  width: 100%;
}

 .logo {
  margin-top: 1.1rem;
  margin-left: 14.0rem;
  margin-bottom: 1.0rem;
  border-radius: 0%;
  -webkit-border-radius: 0%;
  -moz-border-radius: 0%;
  max-width: 220px;
  width: 100%;
}

 .scrollable-sec {
  overflow-x:hidden;
  overflow-y:scroll;
  width: 80%;
  height: 70%;
  padding-right: 10pt;

}

.scrollable-sec::-webkit-scrollbar {
width: 6px;
height: 6px;
}
.scrollable-sec::-webkit-scrollbar-track {
border-radius: 10px;
background: rgba(0,0,0,0.1);
}
.scrollable-sec::-webkit-scrollbar-thumb{
border-radius: 10px;
background: rgba(0,0,0,0.2);
}
.scrollable-sec::-webkit-scrollbar-thumb:hover{
background: rgba(0,0,0,0.4);
}
.scrollable-sec::-webkit-scrollbar-thumb:active{
background: rgba(0,0,0,.9);
}
 
 
